header {
    min-height: 80px;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid #eee;
}

header .logo{
    display: flex;
    align-items: center;
    flex:1
}
header a{
    text-transform: uppsercase;
    color: #555;
}

header ul li{
    display: inline-block;
    opacity: 0.7;
    padding: 0 20px;
}

header ul li:hover{
    opacity:1
}

.menu {
    display: none;
}

.cart-icon{
    position: relative;
    margin-right: 20px;
}
.cart-icon span{
    background: crimson;
    border-radius: 20px;
    color: white;
    position: absolute;
    top:-10px;
    right: -10px;
    padding:5px 7px;
    font-size: 10px;
}

.icon-img{
    width: 56px;
    margin: 5px;
    margin-right: 10px;
}